// @ts-nocheck
import request from "superagent";
import history from "./history";
export const HttpCode = {
    SUCCESS: 200,
    CREATED: 201,
    ACCEPTED: 202,
    NOCONTENT: 204,
    UNAUTHORIZED: 401,
    TOKEN_ERROR: 402,
    BADREQUEST: 400,
    NOTFOUND: 404,
    METHODNOTALLOWED: 405,
    FORBIDDEN: 403,
    ERROR: 500
};

// const apiUrl = "http://localhost:8002/api";
const apiUrl = "https://restaff-test.herokuapp.com/api";

export const agent = {
    post: (url, data) => {
//       return window.fetch(apiUrl + url,{
//           method:"POST",
//           headers:{
//               "Content-type": "Application/json",
//               "authorization": "Token " + localStorage.getItem("token")
//           },
//           body:JSON.stringify(data)
//       })
//       .then(errorHandle)
//       .then(res=>res.json())
    return request
      .post(apiUrl + url)
      .withCredentials()
      .set("Content-type","Application/json")
      .set("authorization", "Token " + localStorage.getItem("token"))
      .send(JSON.stringify(data))
      .then(errorHandle);
  },
  get: (url) => {
    return request
      .get(apiUrl + url)      
      .set("Content-type","Application/json")
      .set("authorization", "Token " + localStorage.getItem("token"))
      .then(errorHandle);
  },
};

function errorHandle(res) {
  if ( res.body &&  res.body.code === HttpCode.UNAUTHORIZED) {
    return history.push("/login");
  }
  return res.body;
}
