// @ts-nocheck
import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Button, Card, CardBody, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from "reactstrap";
import { toast } from "react-toastify";
import Authentication from "../../../utils/Authenticate";
import LoadingOverlay from "react-loading-overlay";
import { HttpCode } from "../../../utils";

class Register extends Component {
  state = {
    username: "",
    password: "",
    confirmPassword: "",
    fullname: "",
    isLoading: false
  };

  constructor(props) {
    super(props);
    this.onChange = this.onChange.bind(this);
    this.register = this.register.bind(this);
  }
  register() {
    let data = { ...this.state };
    if (!data.username || !data.fullname || !data.password) return toast.error("Please fill info for all field");
    if (data.password !== data.confirmPassword) return toast.error("Password and confirm password not match");
    delete data.confirmPassword;
    this.setState({ isLoading: true });
    Authentication.register(data)
      .then(resule => {
        if (resule.code === HttpCode.SUCCESS) {
          this.props.history.push("/");
        } else {
          toast.error(resule.message);
        }
      })
      .catch(error => {
        console.log(error);
        toast.error("An error has occurred, please try again");
      })
      .finally(() => {
        this.setState({ isLoading: false });
      });
  }
  onChange(event) {
    this.setState({ [event.target.name]: event.target.value });
  }
  render() {
    return (
      <div className="app flex-row align-items-center">
        <LoadingOverlay class={"124"} active={this.state.isLoading} spinner text="Loading..." />
        <Container>
          <Row className="justify-content-center">
            <Col md="9" lg="7" xl="6">
              <Card className="mx-4">
                <CardBody className="p-4">
                  <Form>
                    <h1>Register</h1>
                    <p className="text-muted">Create your account</p>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-user" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="text" onChange={this.onChange} name="fullname" placeholder="Fullname" autoComplete="Fullname" />
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-user" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="text" onChange={this.onChange} name="username" placeholder="Username" autoComplete="username" />
                    </InputGroup>
                    <InputGroup className="mb-3">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-lock" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input type="password" onChange={this.onChange} name="password" placeholder="Password" autoComplete="new-password" />
                    </InputGroup>
                    <InputGroup className="mb-4">
                      <InputGroupAddon addonType="prepend">
                        <InputGroupText>
                          <i className="icon-lock" />
                        </InputGroupText>
                      </InputGroupAddon>
                      <Input
                        type="password"
                        onChange={this.onChange}
                        name="confirmPassword"
                        placeholder="Repeat password"
                        autoComplete="new-password"
                      />
                    </InputGroup>
                    <Button color="success" onClick={this.register} block>
                      Create Account
                    </Button>
                    <InputGroup className="mb-4">
                      <p className="text-muted">
                        Have account? <Link to="/login">Login</Link>
                      </p>
                    </InputGroup>
                  </Form>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Register;
