// @ts-nocheck
import React, { Component } from "react";
import { Link } from "react-router-dom";
import { Button, Card, CardBody, CardGroup, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from "reactstrap";
import LoadingOverlay from "react-loading-overlay";
import Authentication from "../../../utils/Authenticate";
import { HttpCode } from "../../../utils";
import { toast } from "react-toastify";

class Login extends Component {
  constructor(props) {
    super(props);
    this.login = this.login.bind(this);
    this.onChange = this.onChange.bind(this);
  }
  state = { isLoading: false, username: "", password: "" };
  onChange(event) {
    this.setState({ [event.target.name]: event.target.value });
  }
  componentWillUnmount(){
    this.setState({ isLoading: false });
  }
  login() {
    if(!this.state.username || !this.state.password )
    return toast.error("Please enter username/password");
    this.setState({ isLoading: true });
    let isSuccess = false;
    Authentication.login(this.state.username, this.state.password)
      .then(data => {
        if (data.code === HttpCode.SUCCESS){ 
          isSuccess = true;
        }
        else{
          toast.error(data.message);
        }
      })
      .catch(error => {
        console.log(error);
        toast.error("An error has occurred, please try again");
      })
      .finally(()=> {
        this.setState({ isLoading: false });
        if( isSuccess ) this.props.history.push("/");
      })
      
  }
  render() {
    return (
      <div className="app flex-row align-items-center">
        <LoadingOverlay active={this.state.isLoading} spinner text="Loading..." />
        <Container>
          <Row className="justify-content-center">
            <Col md="6" lg="4">
              <CardGroup>
                <Card className="p-4">
                  <CardBody>
                    <Form>
                      <h1>Login</h1>
                      <p className="text-muted">Sign In to your account</p>
                      <InputGroup className="mb-3">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-user" />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="text"
                          placeholder="Username"
                          name="username"
                          onChange={this.onChange}
                          value={this.state.username}
                          autoComplete="username"
                        />
                      </InputGroup>
                      <InputGroup className="mb-4">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-lock" />
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input
                          type="password"
                          name="password"
                          placeholder="Password"
                          onKeyPress={(event)=>{
                            if(event.key === 'Enter'){
                              this.login()
                            }
                          }}
                          onChange={this.onChange}
                          value={this.state.password}
                          autoComplete="current-password"
                        />
                      </InputGroup>
                      <Row>
                        <Col xs="6">
                          <Button color="primary" className="px-4" onClick={this.login}>
                            Login
                          </Button>
                        </Col>
                        <Col xs="6" className="text-right">
                          <Link to="/register" color="link" className="px-0">
                            Register
                          </Link>
                        </Col>
                      </Row>
                    </Form>
                  </CardBody>
                </Card>
              </CardGroup>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Login;
